export default class Datetime {
  constructor() {
    this.initDateTime();
  }

  initDateTime() {
    const currentDatetime = new Date().toLocaleString();
    this.render(currentDatetime);

    //Her saniye fonksiyonu recursive olarak ar, bu sayede saat srekli olarak gncellenecek
    setTimeout(() => {
      this.initDateTime();
    }, 500);
  }

  render(datetime) {
    document.querySelector("#date").innerHTML = datetime;
  }
}
