import  {v4 as uuid} from "uuid";
import Header from "./Header"
import Todo from "./Todo"

export default class TodoList {
  /**
   *    Bir TodoList instance' yaratr.
   * Taraycnn "localStorage" ksmnda depolanan "todos" nesnesini eker.
   * Daha sonra listedeki yaplacaklar yaplm olarak iaretlememizi,
   * silmemizi veya listeye yeni bir yaplacak eklememizi salayan
   * "eventleri" oluturur.
   */
  constructor() {
    this.todos = this.getTodos();

    this.render();
  }

  /**
   *    Taraycnn localStorage'ndan "todos" isimli veriyi eker.
   */
  getTodos() {
    const todos = JSON.parse(localStorage.getItem("todos"))
    if(todos === null){
        return []
    } else {
        return todos
    };
  }

  saveTodos() {
      localStorage.setItem("todos", JSON.stringify(this.todos))
  }

  /**
   *    Ekrana st ksmda yer alan ve resim, yenile butonu ve tarih
   * bilgilerini ieren "header" komponentinin, orta ksmda bulunan
   * "todo" komponentlerinin ve en aada bulunan "addTodoForm"
   * komponentinin renderlanmasn salar.
   */
  render() {
    this.setupHeader();

    this.setupTodos();

    this.setupAddNewTodoForm();
  }

  setupTodos() {
    if (this.todos !== null && this.todos.length > 0) {
        document.querySelector("#list").innerHTML = "";
        this.todos.forEach((to_do) => {
            new Todo(to_do.todo, to_do.isComplete, to_do.id);
        });
    }

    this.setEventListeners();
  }

  setupHeader() {
    return new Header();
  }

  setupAddNewTodoForm() {
    const todoInput = document.querySelector("#input");
    todoInput.addEventListener("keypress", (e) => {
      this.handleAddTodoEvent(e);
    });
  }

  /**
   *      Yaplacaklar yani "todo" gelerinin sahip olduu HTML elementlerini seer.
   *  Daha sonra bu elementlere bir "eventListener" ekler. Bu sayede kullanc
   * bu elementler ile etkileime getiinde, "todo" hakknda yaplmas istenen
   * ilemi gerekletiren fonksiyonlar arlm olur.
   */
  setEventListeners() {
      //Bir todo'yu tamamland/tamamlanmad olarak iaretleme
      const toggleButtons = document.querySelectorAll("i[job='toggle']");

      for(let i = 0; i < toggleButtons.length; i++){
          toggleButtons[i].addEventListener("click", e => {
              this.handleToggleTodoEvent(e);
          })
      }

      //Bir todo'yu silme
      const deleteTodoButtons = document.querySelectorAll("i[job='delete']");

      for (let i = 0; i < deleteTodoButtons.length; i++) {
        deleteTodoButtons[i].addEventListener("click", (e) => {
          this.handleDeleteTodoEvent(e);
        });
      }
  }

  /**
   *    Kullanc, bir todo'yu tamamlanm veya tamamlanmam iaretleme butonuna
   * bast zaman bu fonksiyon alr.
   * @param {*} e Event
   */
  handleToggleTodoEvent(e) {
    const todoId = e.target.attributes.todoId.value;

    return this.toggleTodo(todoId);
  }

  /**
   *    Bir todo'nun, tamamlanm veya tamamlanmam olma bilgisine gre deitirilmesini
   * ve uygulamann durumunun gncellenmesini salar.
   * @param {String} todoId Gncellenecek todo'nun id'si.
   */
  toggleTodo(todoId) {
    //TodoList instance'n gncelle
    this.todos = this.todos.map((todo) =>
      todo.id === todoId ? { ...todo, isComplete: !todo.isComplete } : todo
    );

    //Deiiklikleri localStorage'a kaydet
    this.saveTodos();

    //Uygulama durumunu gncelle
    this.setupTodos();
  }

  /**
   *    Kullanc, bir todo'yu silme butonun bast zaman bu fonksiyon alr.
   * @param {*} e Event
   */
  handleDeleteTodoEvent(e) {
    const todoId = e.target.attributes.todoId.value

    return this.deleteTodo(todoId);
  }

  /**
   *    Bir todo'nun, silinmesini ve uygulamann durumunun gncellenmesini salar.
   * @param {String} todoId Silinecek todo'nun id'si.
   */
  deleteTodo(todoId) {
    //TodoList instance'n gncelle
    this.todos = this.todos.filter((todo) => todo.id !== todoId);

    //Deiiklikleri local storage'a kaydet
    this.saveTodos();

    //Uygulama durumunu gncelle
    this.setupTodos();
  }

  /**
   *    Kullanc, yeni yaplacak ekleme formuna bir deer girdikten sonra "enter"
   * tuuna basarsa bu fonksiyon alr.
   * @param {*} e Event
   */
  handleAddTodoEvent(e) {
    //Kullancnn enter tuuna bastn kontrol et
    if (e.keyCode === 13) {
      //Kullancnn bo bir todo oluturmadn kontrol et
      if (e.target.value !== "") {
        //Yeni bir todo objesi olutur
        const newTodo = {
          id: uuid(),
          todo: e.target.value,
          isComplete: false,
        };

        //Oluturulan todo objesini kaydet
        this.addTodo(newTodo);

        //Formu sfrla
        e.target.value = ""
      }
    }
  }

  /**
   *    Yeni bir "todo" objesini hem "TodoList" instance'na hem de
   * taraycnn localStorage'na kaydetmeye yarar
   * @param {{id:String, todo:String, isComplete:boolean}} todo
   */
  addTodo(todo) {
    //TodoList instance'na sz konusu todo'yu ekle
    this.todos.push(todo);

    //Yaplacaklar localStorage'a kaydet
    this.saveTodos();

    //Uygulama durumunu gncelle
    this.setupTodos();
  }
}