import Datetime from "./Datetime"

export default class Header{
    constructor(){
        //Tarih ve saat bilgilerinin renderlanmas
        new Datetime();

        //Temizle butonu iin gerekli event listener'n eklenmesi
        this.setEventListeners()
    }

    setEventListeners(){
        document.querySelector(".clear").addEventListener("click", () => {
            localStorage.clear();
            location.reload()
        })
    }
}